import React, { useState } from 'react';

interface YouTubePlayerProps {
  defaultVideoId?: string;
}

const YouTubePlayer: React.FC<YouTubePlayerProps> = ({ defaultVideoId = 'dQw4w9WgXcQ' }) => {
  const [videoId, setVideoId] = useState(defaultVideoId);
  const [inputValue, setInputValue] = useState('');

  const updateVideo = () => {
    if (inputValue) {
      // Extract video ID from various YouTube URL formats
      let newVideoId = inputValue;
      
      if (inputValue.includes('youtube.com/watch?v=')) {
        const urlParams = new URLSearchParams(inputValue.split('?')[1]);
        newVideoId = urlParams.get('v') || videoId;
      } else if (inputValue.includes('youtu.be/')) {
        newVideoId = inputValue.split('youtu.be/')[1].split('?')[0];
      } else if (inputValue.includes('youtube.com/embed/')) {
        newVideoId = inputValue.split('youtube.com/embed/')[1].split('?')[0];
      }
      
      setVideoId(newVideoId);
      setInputValue('');
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      updateVideo();
    }
  };

  return (
    <div className="w-full max-w-4xl mx-auto">
      <div className="aspect-w-16 aspect-h-9 mb-4">
        <iframe
          src={`https://www.youtube.com/embed/${videoId}`}
          frameBorder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
          className="w-full h-full rounded-lg shadow-lg"
        ></iframe>
      </div>
      <div className="flex space-x-2 mb-6">
        <input
          type="text"
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
          onKeyPress={handleKeyPress}
          placeholder="Enter YouTube URL or video ID"
          className="flex-grow p-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
        <button
          onClick={updateVideo}
          className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
        >
          Update Video
        </button>
      </div>
    </div>
  );
};

export default YouTubePlayer; 